@charset "UTF-8";
*,
::after,
::before {
  @apply box-border break-words;
  word-wrap: break-word;
}

html {
  scroll-behavior: smooth;
}

pre {
  @apply overflow-auto;
}

.block-editor-writing-flow {
  @apply text-base leading-normal;
}

.site-main {
  padding-top: 20px;
}
.site-main .entry-header,
.site-main .entry-footer {
  @apply container;
}
.site-main article,
.site-main nav {
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
}

body {
  font-family: serif;
  font-size: 1.2rem;
  line-height: 1.25;
  background-image: url(../images/checker-bkg.png);
  background-repeat: repeat;
  background-size: 4px;
}
body p,
body h1,
body h2,
body h3,
body h4,
body h5,
body h6,
body li {
  color: #362118;
}

[class^=hl] {
  padding: 0 8px;
}

.hl-yellow {
  background-color: #e6eb1d;
}

.hl-pink {
  background-color: #ff60b1;
}

.hl-blue {
  background-color: #19c6ff;
}

.hl-green {
  background-color: #32ea45;
}

img,
video,
svg {
  max-width: 100%;
  display: block;
  @apply mt-20 mb-20;
}

.wp-block-code {
  @apply container my-16;
}
.wp-block-code code {
  color: #2082c4;
  background-color: #fffbf2;
  border: 1px solid #2082c4;
  @apply p-16;
}

.wp-block-freeform {
  @apply block container;
}

.wp-block-heading {
  @apply block container;
}
.wp-block-heading h1 {
  @apply mb-32;
}
.wp-block-heading h2 {
  @apply mb-24;
}
.wp-block-heading h3 {
  @apply mb-20;
}
.wp-block-heading h4 {
  @apply mb-16;
}
.wp-block-heading h5 {
  @apply mb-12;
}
.wp-block-heading h6 {
  @apply mb-8;
}

.wp-block-html {
  @apply block container;
}

.wp-block-image {
  @apply container relative;
}
.wp-block-image.alignfull,
.wp-block-image .is-resized,
.wp-block-image .aligncenter,
.wp-block-image .alignleft,
.wp-block-image .alignright {
  @apply relative;
}
.wp-block-image.alignfull figcaption {
  width: calc(100% - 2rem);
}

.wp-block-list {
  @apply block container;
}
.wp-block-list ul:not(.menu, .sub-menu) {
  @apply my-16 list-disc list-inside;
}
.wp-block-list ol {
  @apply my-16 list-decimal list-inside;
}
.wp-block-list li li,
.wp-block-list li li li,
.wp-block-list li li li li,
.wp-block-list li li li li li,
.wp-block-list li li li li li li {
  @apply ml-16;
}

.wp-block-pullquote {
  @apply container my-32;
}
.wp-block-pullquote blockquote > p {
  @apply mb-16;
}
.wp-block-pullquote blockquote > cite {
  @apply not-italic;
}

.wp-block-quote {
  @apply container my-32;
}
.wp-block-quote p {
  @apply mb-16;
}
.wp-block-quote cite {
  @apply not-italic;
}

.wp-block-spacer {
  @apply container;
}

.wp-block-table {
  @apply block container mb-24 border-0 border-collapse;
  border-spacing: 0;
}
.wp-block-table table {
  @apply w-full;
}
.wp-block-table thead {
  @apply hidden;
}
@screen desktop {
  .wp-block-table thead {
    @apply table-row-group;
  }
}
.wp-block-table th,
.wp-block-table td {
  @apply p-8 text-center;
}
@screen desktop {
  .wp-block-table th,
  .wp-block-table td {
    @apply table-cell;
  }
}
.wp-block-table td {
  @apply block text-center border border-solid border-black;
}
@screen desktop {
  .wp-block-table td {
    @apply table-cell;
  }
}
.wp-block-table td::before {
  @apply block uppercase;
  content: attr(data-label);
}
@screen desktop {
  .wp-block-table td::before {
    @apply hidden;
  }
}
.wp-block-table th {
  @apply tracking-widest uppercase border border-solid border-black;
}
@screen tablet-portrait {
  .wp-block-table th {
    @apply table-cell;
  }
}
.wp-block-table tr {
  @apply block mb-16;
}
@screen desktop {
  .wp-block-table tr {
    @apply table-row mb-0;
  }
}

.wp-block-gallery {
  @apply block container mb-80;
}
.wp-block-gallery .blocks-gallery-item img,
.wp-block-gallery .blocks-gallery-item figcaption {
  @apply rounded;
}
.wp-block-gallery .blocks-gallery-item figcaption,
.wp-block-image figcaption {
  @apply absolute bottom-0 overflow-auto max-h-full text-white bg-gradient-to-t from-gray-600 pt-32 pb-12 py-12;
}

.wp-caption-text,
.wp-block-table figcaption,
.gallery-caption figcaption,
.wp-block-image figcaption,
.wp-block-embed figcaption,
.wp-block-audio figcaption {
  @apply m-0 text-center text-sm w-full;
