textarea,
input:not(.button) {
  @apply bg-black bg-opacity-10 p-8;
}

label {
  @apply block font-semibold mb-16;
}

.search-form {
  @apply mt-6;
}
.search-form .search-field {
  @apply bg-black bg-opacity-10 text-black p-16 rounded-lg;
}
.search-form .button {
  @apply bg-black bg-opacity-25 text-black p-16 rounded-lg text-center no-underline;
}

.modal {
  @apply inset-0 hidden fixed overflow-hidden;
  z-index: 1050;
}
.modal.modal-open {
  @apply block overflow-x-hidden overflow-y-auto;
}
.modal .modal-dialog {
  @apply absolute w-auto;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.modal .modal-content {
  @apply bg-clip-padding bg-white outline-none p-24 relative;
}
.modal .modal-content .modal-header {
  @apply text-center;
}
.modal .modal-content .modal-body {
  @apply relative;
}
.modal .modal-content .modal-footer {
  @apply relative;
}
.modal .fluid-embed embed,
.modal .fluid-embed object,
.modal .fluid-embed iframe {
  @apply border-none;
}

.close {
  @apply bg-black text-white cursor-pointer block mt-8 outline-none p-8;
}

.off-canvas-container {
  @apply bg-white inset-y-0 h-full fixed;
  overflow-y: auto;
  right: -100%;
  transition: right 0.6s ease-in-out;
  width: 75%;
  z-index: 9998;
}
@screen tablet-portrait {
  .off-canvas-container {
    width: 35vw;
  }
}
.off-canvas-container.is-visible {
  @apply right-0;
}
.admin-bar .off-canvas-container {
  @apply pt-64;
}
.off-canvas-open {
  @apply bottom-0 right-0 block h-24 p-0 absolute w-24;
  background: url(../images/icons/hamburger.svg) 50% 50% no-repeat transparent;
  background-size: 100%;
  right: theme("spacing.12");
  top: theme("spacing.12");
  z-index: 9999;
}
@screen tablet-landscape {
  .off-canvas-open {
    display: none;
  }
}
.admin-bar .off-canvas-open {
  @apply absolute;
  top: theme("spacing.48");
}
.off-canvas-open:focus, .off-canvas-open:hover {
  outline: 2px solid theme("colors.black");
}
.off-canvas-open.is-visible {
  background-image: url(../images/icons/close.svg);
}
.off-canvas-screen {
  @apply bg-black bg-opacity-25 inset-0 fixed invisible;
  z-index: 9996;
}
.off-canvas-screen.is-visible {
  @apply visible;
}
.off-canvas-content {
  @apply hidden mt-20 p-16;
}
.is-visible .off-canvas-content {
  @apply block;
}

.pagination-container {
  @apply container;
}

.svg-sprite-wrapper {
  @apply hidden;
}

