  @apply mt-16;
}

.comments-area {
  @apply container mt-24;
}

.comment-author {
  @apply mb-16 items-center flex;
}
.comment-author .avatar {
  @apply mr-16;
}
.comment-author .fn {
  @apply mr-8;
}

.comment-metadata {
  @apply mb-16;
}

.comment-body {
  @apply py-8;
}

.comment-content {
  @apply bg-black bg-opacity-10 p-16;
}
.bypostauthor .comment-content {
  @apply border-2 border-black border-solid;
}

.comment-list .children {
  @apply ml-16;
}

.reply {
  @apply pt-8;
}

.comment-reply-title a {
  @apply block text-sm;
}

.site-footer .site-info {
  @apply mx-auto my-0 flex justify-between;
}

.site-header {
  background-color: #fffbf2;
  border-bottom: 1px solid #847d7d;
  @apply py-12;
}
.site-header > .site-header-content {
  @apply container;
}
.site-header .site-description {
  @apply no-underline;
}
.site-header .site-branding a {
  @apply no-underline;
}

.entry-meta {
  @apply mb-16;
}

.entry-footer {
  @apply mt-16;
}

.cat-links,
.tag-links {
  @apply block;
